import axios from "axios";
/* import VueSocketIO from 'vue-socket.io';
import SocketIO from 'socket.io-client' */
import "bootstrap/dist/css/bootstrap.min.css";
import Vue from "vue";
import VueRouter from "vue-router";
import App from "./App.vue";
import "./assets/style.bundle.css";
import Display from "./components/Display.vue";
import DisplayPharmacy from "./components/DisplayPharmacy.vue";
const devMode = process.env.NODE_ENV !== "production";

Vue.use(VueRouter);

// Vue.use(
//   new VueSocketIO({
//     debug: process.env.NODE_ENV !== 'production',
//     connection: SocketIO('http://localhost:3000')
//   })
// )

Vue.config.productionTip = false;

const routes = [
  { path: "/display/:id", component: Display },
  { path: "/display-pharmacy/:id", component: DisplayPharmacy },
  { path: '/', redirect: '/display/1' }
];

// 3. Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
const router = new VueRouter({
  routes // short for `routes: routes`
});

axios.defaults.baseURL = devMode
  ? "http://queue-pattani.local"
  : "http://110.78.32.42";
// Add a request interceptor
axios.interceptors.request.use(
  function(config) {
    // Do something before request is sent
    return config;
  },
  function(error) {
    // Do something with request error
    return Promise.reject(error);
  }
);

// Add a response interceptor
axios.interceptors.response.use(
  function(response) {
    // Do something with response data
    return response;
  },
  function(error) {
    // Do something with response error
    return Promise.reject(error);
  }
);

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");
