<template>
  <div id="display-pharmacy">
    <div v-if="loading" class="loading">
        <div class="loader">Loading...</div>
    </div>
    <div class="row display">
        <div class="col-xs-1 col-sm-1 col-md-1 col-lg-1 no-padding margin-auto">
            <!-- logo -->
            <div class="display-header-img">
                <img src="../assets/pattani-logo.png"
                     alt="display-logo"
                     class="img-responsive display-logo center-block"/>
            </div>
        </div>
        <!-- header -->
        <div class="col-xs-4 col-sm-4 col-md-4 col-lg-4 no-padding border-right-pink margin-auto hospital-title">
            <div class="header-label">
                <h1 class="color-blue header-label-th faa-horizontal animated">
                    {{ hospital_name_th }}
                </h1>
                <p class=" no-padding no-margins hidden-sm hidden-xs"></p>
                <h2 class="color-blue no-margins header-label-en faa-horizontal animated">
                    {{ hospital_name_en }}
                </h2>
            </div>
        </div>
        <div class="col-xs-4 col-sm-4 col-md-4 col-lg-4 no-padding margin-auto">
            <div class="header-date-info text-center">
                <h1 class="color-blue header-label-date">
                    {{ current_date_time }}
                </h1>
            </div>
        </div>
        <div class="col-xs-3 col-sm-3 col-md-3 col-lg-3 no-padding margin-auto">
            <div class="header-clock color-blue">
                <span class="color-blue header-label-clock">
                    <i class="fa fa-clock-o"></i>
                </span>
                <span ref="hours" class="hours">{{ hours }}</span>:<span ref="min"
                                                                         class="min">{{ min }}</span>:<span
                        ref="sec" class="sec">{{ sec }}</span>
            </div>
        </div>
    </div>
    <!--begin:-->
    <div v-show="!loading" class="row no-margin no-padding no-border">
        <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12 no-margin no-padding no-border">
            <table class="table table-bordered no-footer dtr-inline no-border">
                <thead>
                <tr>
                    <th class="text-center no-padding no-border" style="width: 100%; vertical-align: middle;">
                        <div class="sectionTitle">
                            <h2 class="wow animated">
                                <span class="shape shape-left bg-color-4"></span>
                                <span class="section-title font-bold">
                                      คิวห้องจ่ายยา
                                    </span>
                                <span class="shape shape-right bg-color-4"></span>
                            </h2>
                        </div>
                    </th>
                </tr>
                </thead>
            </table>
        </div>
    </div>
    <!--end:-->
    <!--begin:-->
    <div v-show="!loading" class="row">
        <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6 col-table-left">
            <table class="table table-bordered no-footer dtr-inline no-border">
                <tbody>
                <tr v-for="(item, index) in filteredDataTableLeft" :key="index">
                    <td :class="['w-50 text-center color-white font-bold', {'row-blink': isRowHighLight(item.caller_id)}]">
                            <span :class="['queue-number float-left', {blink: isBlink(item.caller_id)}]">
                                {{ item.queue_no }}
                            </span>
                        <span :class="['queue-counter float-right', {blink: isBlink(item.caller_id)}]">
                                {{ item.counter_service_name }}
                            </span>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6 col-table-right">
            <table class="table table-bordered no-footer dtr-inline no-border">
                <tbody>
                <tr v-for="(item, index) in filteredDataTableRight" :key="index">
                    <td :class="['w-50 text-center color-white font-bold', {'row-blink': isRowHighLight(item.caller_id)}]">
                            <span :class="['queue-number float-left', {blink: isBlink(item.caller_id)}]">
                                {{ item.queue_no }}
                            </span>
                        <span :class="['queue-counter float-right', {blink: isBlink(item.caller_id)}]">
                                {{ item.counter_service_name }}
                            </span>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
    <!--end:-->
    <!--footer-->
    <div v-show="!loading" class="row hold-content">
        <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6 no-margin no-border text-center">
            <div class="font-bold">
                <h1 class="hold-label color-blue">
                    {{ hold_label }}
                </h1>
            </div>
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6 col-lg-6 no-margin no-padding no-border">
            <div class="marquee-left">
                <h1 class="marquee font-bold color-blue">
                    {{ filteredDataHold }}
                </h1>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
import moment from "moment";
import io from 'socket.io-client';
import "font-awesome/css/font-awesome.min.css";
moment.locale("th");
const devMode = process.env.NODE_ENV !== 'production';

const SOCKET_EVENTS = {
  ON_REGISTER: "REGISTER",
  ON_CALL_WAIT: "CALL_WAIT",
  ON_END_WAIT: "END_WAIT",
  ON_RECALL: "RECALL",
  ON_HOLD: "HOLD",
  ON_END: "END",
  ON_CALL_HOLD: "CALL_HOLD",
  ON_END_HOLD: "END_HOLD",
  DEVICE_CONNECTED: "DEVICE_CONNECTED",
  CARD_INSERTED: "CARD_INSERTED",
  READING_START: "READING_START",
  CARD_REMOVED: "CARD_REMOVED",
  DEVICE_DISCONNECTED: "DEVICE_DISCONNECTED",
  READING_COMPLETE: "READING_COMPLETE",
  READING_FAIL: "READING_FAIL",
  CLOSE_PRINT: "CLOSE_PRINT",
  UPDATED_SETTINGS: "UPDATED_SETTINGS",
  AUDIO_PLAYING: "AUDIO_PLAYING",
  AUDIO_ENDED: "AUDIO_ENDED",
  UPDATED_QUEUE: "UPDATED_QUEUE"
};

export default {
  data() {
    return {
      loading: false,
      hospital_name_th: "โรงพยาบาลปัตตานี",
      hospital_name_en: "PATTANI HOSPITAL",
      service_ids: [],
      counter_service_ids: [],
      services: null,
      counter_services: null,
      display: null,
      defaultData: {
        queue_no: "คิว -",
        counter_service_name: "ช่อง -",
        caller_id: "",
        call_time: "",
        service_type_id: null
      },
      rows: [],
      pageLength: 4,
      playingId: null, // caller_id
      highlightId: null,
      rowsHold: [],
      player: null,
      myPlaylist: null,
      myPlayer: null,
      socket: io(devMode ? 'http://localhost:3000' : 'http://110.78.32.42:3000')
    };
  },
  computed: {
    current_date_time: function () {
      return (
          moment().format('วันdddที่ D MMMM ') + (parseInt(moment().format('YYYY')) + 543)
      )
    },
    hours: function () {
      return moment().format('HH')
    },
    min: function () {
      return moment().format('mm')
    },
    sec: function () {
      return moment().format('ss')
    },
    hold_label: function () {
      if (!this.display) return 'คิวที่เรียกไปแล้ว';
      return this.display.hold_label;
    },
    // คิวซักประวัติ
    filteredAllData: function () {
      const vm = this;
      const rows = [];
      const dataLength = vm.rows.length;
      if (dataLength > 0) {
        for (let index in vm.rows) {
          // ถ้าข้อมูลมีน้อยกว่าจำนวนคิวที่ต้องการแสดง
          if (parseInt(vm.rows[index].service_type_id) === 3) { // 1 คิวจ่ายยา
            rows.push(vm.rows[index]);
          }
        }
      }
      return rows ? vm.sortRows(rows, 'call_time', -1) : [];
    },
    filteredDataTableLeft: function () {
      const vm = this;
      const allData = vm.filteredAllData;
      const pageLength = vm.pageLength;
      const rows = [];
      // ถ้าไม่มีข้อมูล
      if (allData.length < 1) {
        for (let i = 0; i < pageLength; i++) {
          rows.push({
            queue_no: 'คิว -',
            counter_service_name: 'ช่อง -',
            caller_id: '',
            call_time: '',
            service_type_id: null
          });
        }
      } else {
        for (let index in allData) {
          // ถ้าข้อมูลมีน้อยกว่าจำนวนคิวที่ต้องการแสดง
          if (rows.length < pageLength) {
            rows.push(allData[index]);
          } else {
            break
          }
        }
      }
      // ถ้าข้อมูลมีน้อยกว่าจำนวนคิวที่ต้องการแสดง ให้เพิ่มรายการให้ครบตามจำนวนที่ต้องการแสดง
      if (rows.length < pageLength) {
        for (let i = rows.length; i < pageLength; i++) {
          rows.push({
            queue_no: 'คิว -',
            counter_service_name: 'ช่อง -',
            caller_id: '',
            call_time: '',
            service_type_id: null
          });
        }
      }
      return vm.sortRows(rows, 'call_time', -1);
    },
    // คิวห้องตรวจ
    filteredAllDataRight: function () {
      const vm = this;
      const rows = [];
      const dataLength = vm.rows.length;
      if (dataLength > 0) {
        for (let index in vm.rows) {
          // ถ้าข้อมูลมีน้อยกว่าจำนวนคิวที่ต้องการแสดง
          if (parseInt(vm.rows[index].service_type_id) === 2) { // 2 คิวห้องตรวจ
            rows.push(vm.rows[index]);
          }
        }
      }
      return vm.sortRows(rows, 'call_time', -1);
    },
    filteredDataTableRight: function () {
      const vm = this;
      const allData = vm.filteredAllData;
      const pageLength = vm.pageLength;
      const rows = [];
      // ถ้าไม่มีข้อมูล
      if (allData.length < 1) {
        for (let i = 0; i < pageLength; i++) {
          rows.push({
            queue_no: 'คิว -',
            counter_service_name: 'ช่อง -',
            caller_id: '',
            call_time: '',
            service_type_id: null
          });
        }
      } else {
        for (let index in allData) {
          // ถ้าข้อมูลมีน้อยกว่าจำนวนคิวที่ต้องการแสดง
          if ((parseInt(index) + 1) > pageLength) {
            rows.push(allData[index]);
          } else if(rows.length === pageLength) {
            break
          }
        }
      }
      // ถ้าข้อมูลมีน้อยกว่าจำนวนคิวที่ต้องการแสดง ให้เพิ่มรายการให้ครบตามจำนวนที่ต้องการแสดง
      if (rows.length < pageLength) {
        for (let i = rows.length; i < pageLength; i++) {
          rows.push({
            queue_no: 'คิว -',
            counter_service_name: 'ช่อง -',
            caller_id: '',
            call_time: '',
            service_type_id: null
          });
        }
      }
      return vm.sortRows(rows, 'call_time', -1);
    },
    filteredDataHold: function () {
      const vm = this;
      const rowsHold = vm.rowsHold;
      let rows = [];
      for (let i in rowsHold) {
        rows.push(rowsHold[i].queue_no)
      }
      return rows.join(' ');
    },
    isPlayer: function () {
      return !this.isEmpty(this.player);
    }
  },
  mounted: function () {
    this.initClock();
    this.initSocketEvents();
    this.fetchDataDisplay();
  },
  methods: {
    initClock: function () {
      const hours = this.$refs.hours;
      const min = this.$refs.min;
      const sec = this.$refs.sec;
      const d = new Date();
      d.setDate(d.getDate());
      if (sec) {
        this.timesec = setInterval(function () {
          const b = new Date().getSeconds();
          sec.innerHTML = (b < 10 ? '0' : '') + b
        }, 1e3)
      }
      if (min) {
        this.timemin = setInterval(function () {
          const c = new Date().getMinutes();
          min.innerHTML = (c < 10 ? '0' : '') + c
        }, 1e3)
      }
      if (hours) {
        this.timehours = setInterval(function () {
          const d = new Date().getHours();
          hours.innerHTML = (d < 10 ? '0' : '') + d
        }, 1e3)
      }
    },
    initSocketEvents: function () {
      const vm = this;

      // เล่นเสียง
      vm.socket.on(SOCKET_EVENTS.AUDIO_PLAYING, function(data) {
        const {service, counter, caller} = data.data;
        if (vm.isMatchServiceAndCounterService(service, counter)) {
          const rows = vm.rows;
          // ค้นหาคิวที่เรียกว่าอยู่ในรายการที่แสดงผลหรือไม่
          const index = rows.findIndex(item => {
            return item.caller_id.toString() === caller.caller_id.toString()
          });
          // จำนวนรายการที่แสดง
          const pageLengthAll = vm.pageLength * 2;
          // ถ้ารายการที่เรียกมีอยู่ในรายการที่กำลังแสดง
          if (index > -1 && index + 1 > pageLengthAll) {
            // ตรวจสอบว่ามีอยู่ในรายการคิวที่แสดงผลหรือยัง
            let newRows = rows.filter(item => {
              return (item.caller_id.toString() !== caller.caller_id.toString());
            });
            const updatedObj = vm.mapDataObject(data);
            newRows.push(updatedObj);
            vm.rows = vm.sortRows(newRows, 'call_time', -1); // เรียงตามเวลาเรียกล่าสุดขึ้นก่อน
          } else if (index === -1) {
            const updatedObj = vm.mapDataObject(data);
            let newRows = [];
            newRows.push(...rows);
            newRows.push(updatedObj);
            vm.rows = vm.sortRows(newRows, 'call_time', -1); // เรียงตามเวลาเรียกล่าสุดขึ้นก่อน
          } else {
            const newRows = rows.map(item => {
              const oldCallerId = item.caller_id.toString();
              const newCallerId = caller.caller_id.toString();
              if (oldCallerId === newCallerId) {
                item = vm.mapDataObject(data)
              }
              return item;
            });
            vm.rows = vm.sortRows(newRows, 'call_time', -1); // เรียงตามเวลาเรียกล่าสุดขึ้นก่อน
          }
          // แถวที่ highlight ว่ากำลังเรียกคิวอยู่
          vm.highlightId = null;
          // แถวที่กำลังกระพริบ
          vm.playingId = caller.caller_id;
          // อัพเดทคิวพัก
          vm.rowsHold = vm.rowsHold.filter(item => {
            return item.caller_id.toString() !== caller.caller_id.toString()
          });
        }
      });

      //
      vm.socket.on(SOCKET_EVENTS.AUDIO_ENDED, function(data) {
        const {service, counter, caller} = data.data;
        if (vm.isMatchServiceAndCounterService(service, counter)) {
          // ตรวจสอบว่าคิวกำลังกระพิบตรงกับคิวที่เสียงเรียกเสร็จหรือไม่?
          if (!vm.isEmpty(vm.playingId) && vm.playingId === caller.caller_id) {
            vm.playingId = null;
            vm.highlightId = caller.caller_id;
          }
        }
      });

      // เสร็จสิ้นคิว รอเรียก
      vm.socket.on(SOCKET_EVENTS.ON_END_WAIT, function(data) {
        const {service, counter, caller} = data.data;
        if (vm.isMatchServiceAndCounterService(service, counter)) {
          const rows = vm.rows;
          const newRows = rows.filter(item => {
            return item.caller_id.toString() !== caller.caller_id.toString()
          });
          // อัพเดทรายการใหม่
          vm.rows = this.sortRows(newRows, 'call_time', -1);
        }
      });

      // เสร็จสิ้นคิวกำลังเรียก
      vm.socket.on(SOCKET_EVENTS.ON_END, function(data) {
        const {service, counter, caller} = data.data;
        if (vm.isMatchServiceAndCounterService(service, counter)) {
          const rows = vm.rows;
          const newRows = rows.filter(item => {
            return item.caller_id.toString() !== caller.caller_id.toString()
          });
          // อัพเดทรายการใหม่
          vm.rows = this.sortRows(newRows, 'call_time', -1);

          vm.rowsHold = vm.rowsHold.filter(item => {
            return item.caller_id.toString() !== caller.caller_id.toString()
          });
        }
      });

      // เรียกคิว คิวพัก
      vm.socket.on(SOCKET_EVENTS.ON_CALL_HOLD, function(data) {
        const {service, counter, caller} = data.data;
        if (vm.isMatchServiceAndCounterService(service, counter)) {
          const rows = vm.rows;
          const newRows = rows.filter(item => {
            return item.caller_id.toString() !== caller.caller_id.toString()
          });
          // อัพเดทรายการใหม่
          vm.rows = this.sortRows(newRows, 'call_time', -1);

          vm.rowsHold = vm.rowsHold.filter(item => {
            return item.caller_id.toString() !== caller.caller_id.toString()
          });
        }
      });

      vm.socket.on(SOCKET_EVENTS.ON_HOLD, function(data) {
        const {service, counter, caller} = data.data;

        const callerId = caller.caller_id.toString();
        const highlightId = vm.highlightId;
        const playingId = vm.playingId;

        if (vm.isMatchServiceAndCounterService(service, counter)) {
          const rows = vm.rows;
          // ลบรายการที่ hold
          const newRows = rows.filter(item => {
            return item.caller_id.toString() !== callerId
          });
          // ตรวจสอบว่าคิวที่ถูกพักคิว highlight อยู่หรือไม่
          if (!vm.isEmpty(highlightId) && highlightId.toString() === callerId) {
            vm.highlightId = null;
          }
          // ตรวจสอบว่าคิวที่ถูกพักคิว กระพิบหรือกำลังเรียก อยู่หรือไม่
          if (!vm.isEmpty(playingId) && playingId.toString() === callerId) {
            vm.playingId = null;
          }
          const updatedObj = vm.mapDataObject(data);
          const rowsHold = [];
          rowsHold.push(...vm.rowsHold);
          rowsHold.push(updatedObj);
          vm.rowsHold = rowsHold;
          // อัพเดทรายการใหม่
          vm.rows = this.sortRows(newRows, 'call_time', -1);
        }
      });
    },
    fetchDataDisplay: function () {
      const vm = this;
      vm.loading = true;
      axios
        .get("/kiosk/default/data-display?id=" + vm.$route.params.id)
        .then(function(response) {
          // handle success
          const { data } = response;
          const css = document.createElement("style");
          css.setAttribute("id", "style-display");
          css.appendChild(document.createTextNode(data.model.display_css));
          document.head.appendChild(css);
          document.title = data.model.display_name;
          vm.display = data.model;
          vm.pageLength = data.model.page_length;
          vm.service_ids = data.service_ids;
          vm.counter_service_ids = data.counter_service_ids;
          vm.services = data.services;
          vm.counter_services = data.counter_services;
          vm.loading = false;
          vm.fetchDataQueue();
        })
        .catch(function(error) {
          vm.loading = false;
          vm.handleError(error);
        });
    },
    fetchDataQueue: function () {
      const vm = this;
      const bodyFormData = new FormData();
      bodyFormData.append("service_ids", vm.service_ids);
      bodyFormData.append("counter_service_ids", vm.counter_service_ids);
      axios
        .post("/kiosk/default/queue-display", bodyFormData)
        .then(function(response) {
          if (!vm.isEmpty(response.data)) {
            vm.rows = vm.sortRows(response.data.rows_call, "call_time", -1);
            vm.rowsHold = response.data.rows_hold;
          }
        })
        .catch(function(error) {
          vm.handleError(error);
        });
    },
    isMatchServiceAndCounterService: function (service, counter) {
      if (this.isEmpty(this.service_ids) && this.isEmpty(this.counter_service_ids)) return false;
      return this.service_ids.includes(service.service_id.toString()) && this.counter_service_ids.includes(counter.counter_service_id.toString());
    },
    isMatchDataPlayer: function (service, counter) {
      if (!this.isPlayer) return false;
      return this.player.service_ids.includes(service.service_id.toString()) && this.player.counter_service_ids.includes(counter.counter_service_id.toString());
    },
    updateObject(oldObject, updatedProperties) {
      return {
        ...oldObject,
        ...updatedProperties
      }
    },
    isEmpty: function (value, trim) {
      return value === null || value === undefined || value.length === 0 || (trim === '') || (value === '');
    },
    isRowHighLight(callerId) {
      if (this.isEmpty(this.highlightId)) return false;
      return this.highlightId === callerId
    },
    isBlink(callerId) {
      if (this.isEmpty(this.playingId)) return false;
      return this.playingId === callerId
    },
    mapDataObject(data) {
      const {queue, counter, caller, queue_detail} = data.data;
      return {
        queue_no: queue.queue_no,
        counter_service_name: counter.counter_service_name,
        caller_id: caller.caller_id,
        call_time: caller.call_time,
        service_type_id: queue_detail.service_type_id
      }
    },
    // เรียงข้อมูล
    sortRows: function (rows, key, order) {
      return rows.slice().sort(function (a, b) {
        a = a[key];
        b = b[key];
        return (a === b ? 0 : a > b ? 1 : -1) * order
      })
    },
    handleError: function(error) {
      let message = "Error";
      if (error.response) {
        message = this.getErrorMessage(error);
        Swal.fire({
          type: "error",
          title: "Error...",
          text: message || "Error",
          showConfirmButton: false,
          timer: 3000
        });
      } else if (error.request) {
        Swal.fire({
          type: "error",
          title: "Error...",
          text: error.message || "Error",
          showConfirmButton: false,
          timer: 3000
        });
      } else {
        Swal.fire({
          type: "error",
          title: "Error...",
          text: error.message || "Error",
          showConfirmButton: false,
          timer: 3000
        });
      }
    },
    getErrorMessage: function(error) {
      let message = "";
      if (error.response) {
        if (error.response.hasOwnProperty("data")) {
          const { data } = error.response;
          if (data.message) {
            message = data.message;
          } else if (data.errors) {
            message = data.errors;
          } else if (data.statusText) {
            message = data.statusText;
          }
        } else if (error.message) {
          message = error.message;
        }
      }
      return message;
    }
  }
};
</script>


